import { DEGREE12_FLAT_LIST, DEGREE12_SHARP_LIST, DIATONIC_MAJOR_SCALE_DEGREE_NAMES, KEY12_FLAT_LIST, KEY12_MAJOR_SCALE_LIST, KEY12_MINOR_SCALE_LIST, KEY12_SHARP_LIST, SCALE_LIST, SymbolKey, SYMBOL_PARAMS_LIST } from "contents/common/define";
import { GlobalContext } from "contents/MainFrame";
import { DetailChord, DetailFixed, DetailInit, DetailModulation, DetailSection, CHORD_MODE, NoteElement, TypeNoteElement, ScoreProgress } from "contents/redux/store";
import { useContext } from "react";

type ScoreElementDetailProps = {
    index: number;
    noteElement: NoteElement;
}

const ScoreElementDetail = (props: ScoreElementDetailProps) => {

    const { state, dispatcher } = useContext(GlobalContext);

    const chordMode = state.noteData.chordMode;
    const cache = state.noteState.cacheList[props.index];

    switch (props.noteElement.type) {
        case TypeNoteElement.Fixed: {
            const detail = props.noteElement.detail as DetailFixed;
            return (
                <span>{detail.label}</span>
            );
        }
        case TypeNoteElement.Init: {
            const detail = props.noteElement.detail as DetailInit;
            const paramLabels: JSX.Element[] = [];
            paramLabels.push(
                <div key={paramLabels.length}>
                    <span>Tempo: </span>
                    <span>{detail.tempo}</span>
                </div>
            );
            paramLabels.push(
                <div key={paramLabels.length}>
                    <span>Beat: </span>
                    <span>{detail.rythm}</span>
                </div>
            );
            if (chordMode == CHORD_MODE.SCALE) {
                paramLabels.push(
                    <div key={paramLabels.length}>
                        <span>Key: </span>
                        {/* <span>{detail.key}</span> */}
                        <span>{
                            detail.scale == SCALE_LIST[0] ?
                                KEY12_MAJOR_SCALE_LIST[detail.keyIndex] :
                                KEY12_MINOR_SCALE_LIST[detail.keyIndex]
                        }</span>
                    </div>
                );
                paramLabels.push(
                    <div key={paramLabels.length}>
                        <span>Scale: </span>
                        <span>{detail.scale}</span>
                    </div>
                );
            }
            return (
                <div>
                    {paramLabels}
                </div>
            );
        }
        case TypeNoteElement.Chord: {
            const detail = props.noteElement.detail as DetailChord;

            /** */
            const getDegreeRoot = (index: number): string => {
                let name = 'blank';
                if (index != -1) {
                    const list = detail.isFlat ? DEGREE12_FLAT_LIST : DEGREE12_SHARP_LIST;
                    name = list[index];
                }
                return name;
            }
            /** */
            const getChordName = (): string => {
                const degreeIndex = detail.degreeIndex;
                let name = '-';
                if (degreeIndex != -1) {
                    const keyIndex = cache.init.keyIndex;
                    const list = detail.isFlat ? KEY12_FLAT_LIST : KEY12_SHARP_LIST;
                    name = list[(degreeIndex + keyIndex) % 12];
                }
                return name;
            }
            /** */
            const getSymbol = (index: number, symbolKey: SymbolKey): string => {
                let name = '';
                if (index != -1) {
                    SYMBOL_PARAMS_LIST.forEach((symbol) => {
                        if (symbol.key == symbolKey) {
                            name = symbol.name;
                        }
                    });
                }
                return name;
            }

            const degreeRoot = getDegreeRoot(detail.degreeIndex);
            const chordName = getChordName();
            const symbolName = getSymbol(detail.degreeIndex, detail.symbolKey);
            const isDiatonic = DIATONIC_MAJOR_SCALE_DEGREE_NAMES.includes(degreeRoot + symbolName);
            const sustainJsx = [];
            for (let i = 0; i < detail.sustain; i++) {
                sustainJsx.push(<span key={i}></span>);
            }
            const barCount = cache.progress.barCount;
            const remaindBeat = cache.progress.remaindBeat;
            const headMSec = Math.round(cache.progress.headMSec / 1000 * 10) / 10;
            // alert(detail.degreeIndex);
            const notesCount = dispatcher.backing.getNotesCount(detail.backing);
            return (
                <>
                    <div className={
                        'degree'
                        + (detail.degreeIndex == -1 ? ' blank' : '')
                        + (isDiatonic ? ' diatonic' : '')
                        + (state.noteData.chordMode == CHORD_MODE.DIRECT ? ' visible' : '')}>
                        <span>{degreeRoot}{symbolName}</span>
                    </div>
                    <div className={state.noteData.chordMode == CHORD_MODE.DIRECT ? 'degree' : 'chord'}>
                        <span>{chordName}{symbolName}</span>
                    </div>
                    <div className="sustain">
                        {sustainJsx}
                    </div>
                    <div className="progress-sustain">
                        <span className="bar">{barCount != -1 ? barCount : ''}</span>
                        <span className="beat">{remaindBeat != -1 ? remaindBeat : ''}</span>
                    </div>
                    <div className="progress-time">
                        <span>{headMSec}s</span>
                    </div>
                    <div
                        className={
                            'backing-script' +
                            (notesCount == 0 ? ' hidden' : '')
                        }
                    >
                        <span>notes: {notesCount}</span>
                    </div>
                </>
            );
        }
        case TypeNoteElement.Section: {
            const detail = props.noteElement.detail as DetailSection;
            return (
                <>
                    <div className="name"><span>{detail.labelName}</span></div>
                    <div className="border"></div>
                </>
            );
        }
        case TypeNoteElement.Modulation: {
            const detail = props.noteElement.detail as DetailModulation;
            const paramLabels: JSX.Element[] = [];
            paramLabels.push(
                <div key={paramLabels.length}>
                    <span>Tempo: </span>
                    <span>{detail.tempo}</span>
                </div>
            );
            paramLabels.push(
                <div key={paramLabels.length}>
                    <span>Beat: </span>
                    <span>{detail.timeSignatures}</span>
                </div>
            );
            if (chordMode == CHORD_MODE.SCALE) {
            }
            return (
                <div>
                    {paramLabels}
                </div>
            );
        }
    }
}

export default ScoreElementDetail;