
import { DEGREE12_SHARP_LIST, KEY12_SHARP_LIST, SCALE_LIST, SYMBOL_PARAMS_LIST } from 'contents/common/define';
import { getSymbolParams } from 'contents/common/utils';
import { GlobalContext } from 'contents/MainFrame';
import { DetailChord, TypeNoteElement } from 'contents/redux/store';
import { useContext } from 'react';
import './CurrentElementInfo.css';

type DataRecord = {
    title: string;
    value: string;
    disable: boolean;
}

const CurrentElementInfo = () => {

    const { state, dispatch } = useContext(GlobalContext);
    const focusIndex = state.noteState.focusIndex;
    if (focusIndex == -1) return (<></>);

    const data = state.noteData.dataList[focusIndex];
    const cache = state.noteState.cacheList[focusIndex];

    let inner: JSX.Element = <></>;
    switch (data.type) {
        case TypeNoteElement.Chord: {
            const detail = data.detail as DetailChord;
            let root = '-';
            if (detail.degreeIndex != -1) {
                const degreeRoot = DEGREE12_SHARP_LIST[detail.degreeIndex];
                const chordRoot = KEY12_SHARP_LIST[detail.degreeIndex];
                root = `${degreeRoot} (${chordRoot})`;
            }
            let symbolName = '-';
            if (detail.symbolKey != null) {
                symbolName = `[${getSymbolParams(detail.symbolKey)?.name as string}]`;
            }

            const structList: JSX.Element[] = [];
            if (detail.symbolKey != null) {
                const symbol = getSymbolParams(detail.symbolKey);
                const list = symbol?.intervals;
                const len = list?.length as number;
                for (let i = 0; i < 7; i++) {
                    if (i < len) {
                        const interval = symbol?.intervals[i] as number;
                        const struct = symbol?.struct[i];
                        const sound = KEY12_SHARP_LIST[(detail.degreeIndex + interval) % 12];
                        structList.push(
                            <div key={i} className="record">
                                <div className="interval"><span>{interval}</span></div>
                                <div className="struct"><span>{struct}</span></div>
                                <div className="sound"><span>{sound}</span></div>
                            </div>
                        );
                    } else {
                        structList.push(
                            <div key={i} className="record disable">
                                <div className="interval"></div>
                                <div className="struct"></div>
                                <div className="sound"></div>
                            </div>
                        );
                    }
                };
            }


            const baseBlockJsx = buildInfoBlock('Base', [
                { title: 'Tempo', value: '' + cache.init.tempo, disable: false },
                { title: 'Rythm', value: cache.init.rythm, disable: false },
                { title: 'Key', value: KEY12_SHARP_LIST[cache.init.keyIndex], disable: false },
                { title: 'Scale', value: cache.init.scale, disable: false },
            ]);

            const chordBlockJsx = buildInfoBlock('Chord', [
                { title: 'Root', value: root, disable: root == '-' },
                { title: 'Symbol', value: symbolName, disable: symbolName == '-' },
                { title: 'On chord', value: '-', disable: true },
                { title: 'Sustain', value: String(detail.sustain), disable: false },
            ]);

            inner = (
                <div
                    className={
                        'chord'
                    }
                >
                    <div>
                        {baseBlockJsx}
                        {chordBlockJsx}
                        <div className="title">
                            <span>Struct</span>
                        </div>
                        <div className="symbol-struct">
                            {structList}
                        </div>
                    </div>
                </div>
            )
        }
            break;
        case TypeNoteElement.Section: {


            // const baseBlockJsx = buildInfoBlock('Base', [
            //     { title: 'Tempo', value: '' + cache.init.tempo, disable: false },
            //     { title: 'Rythm', value: cache.init.rythm, disable: false },
            //     { title: 'Key', value: KEY12_SHARP_LIST[cache.init.keyIndex], disable: false },
            //     { title: 'Scale', value: cache.init.scale, disable: false },
            // ]);
            inner = (
                <div
                    className={
                        'section'
                    }
                >
                    {/* {baseBlockJsx} */}
                </div>
            )
        }
            break;
    }
    return (
        <div id="current-detail">
            {inner}
        </div>
    );
}

export default CurrentElementInfo;


const buildInfoBlock = (titleLabel: string, dataRecordList: DataRecord[]): JSX.Element => {
    const jsxList: JSX.Element[] = [];
    dataRecordList.forEach((state, i) => {
        jsxList.push(
            <div
                key={i}
                className={
                    'record' +
                    (state.disable ? ' disable' : '')
                }>
                <div className="title"><span>{state.title}:</span></div>
                <div className="value"><span>{state.value}</span></div>
            </div>
        );
    });
    return (<>
        <div className="title">
            <span>{titleLabel}</span>
        </div>
        <div className="current-state">
            {jsxList}
        </div>
    </>);
}