import { DIATONIC_MAJOR_SCALE_LIST, KEY12_SHARP_LIST, SYMBOL_PARAMS_LIST } from "contents/common/define";
import { getSymbolParams } from "contents/common/utils";
import { Dispatcher } from "contents/redux/dispatcher";
import { ActionType } from "contents/redux/reducer";
import { BackingEditorState as BackingEditorState, BACKING_LEN_TYPE, BACKING_SIDE, CHORD_MODE, DetailChord, DetailSection, EditMode, FloatWndState, NoteElement, Store, TypeNoteElement } from "contents/redux/store";
import { KeyboardEvent } from 'react';


/**
 * 
 * @param e 
 */
export const KeyboardShortCutAction = (e: KeyboardEvent, state: Store, dispatch: Function, dispatcher: Dispatcher) => {
    // alert(e.key);

    if (state.scoreTabIndex != 1) {
        return;
    }

    const focusIndex = state.noteState.focusIndex;
    const editMode = state.noteState.editMode;
    const chordMode = state.noteData.chordMode;
    const selectedData = state.noteData.dataList[focusIndex];

    switch (selectedData.type) {
        case TypeNoteElement.Section: {
            const detail = selectedData.detail as DetailSection;

            switch (state.noteState.floatwndState) {
                case FloatWndState.NONE: {

                    if (editMode == EditMode.NORMAL) {
                        switch (e.key) {
                            case 'c': {
                                dispatcher.score.callSectionEditor();
                            }
                                break;
                        }
                    }
                }
                    break;
                case FloatWndState.SECTION_PALETTE: {
                    switch (e.key) {
                        case 'Escape':
                        case 'c':
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                            break;
                    }
                }
                    break;
            }
        }
            break;
        case TypeNoteElement.Chord: {
            const detail = selectedData.detail as DetailChord;

            switch (state.noteState.floatwndState) {
                case FloatWndState.NONE: {

                    if (editMode == EditMode.NORMAL) {
                        switch (e.key) {
                            case 'c': {
                                dispatcher.score.callChordSelector();
                            }
                                break;
                            case 'b': {
                                if (detail.degreeIndex != -1) {
                                    dispatcher.score.callBackingEditor();
                                }
                            }
                                break;
                            case '1':
                            case '2':
                            case '3':
                            case '4':
                            case '5':
                            case '6':
                            case '7': {
                                if (chordMode == CHORD_MODE.DIRECT) break;
                                const diatonicChord = DIATONIC_MAJOR_SCALE_LIST[Number(e.key) - 1];
                                dispatch(ActionType.SET_DEGREE_INDEX, { pos: focusIndex, degreeIndex: diatonicChord.degreeIndex });
                                dispatch(ActionType.SET_SYMBOL_KEY, { pos: focusIndex, symbolKey: diatonicChord.symbolKey });
                                dispatcher.score.apllyFlashCur();
                            }
                                break;
                            case 'x': {
                                dispatcher.score.setCriteria();
                            }
                                break;
                            case ' ': {
                                dispatcher.backing.play(true);
                            }
                                break;
                        }
                    } else if (editMode == EditMode.BACKING) {

                        switch (e.key) {
                            case 'x': {
                                dispatcher.score.releaseCriteria();
                            }
                                break;
                            case 'Enter': {
                                const dataList = state.noteData.dataList;
                                const baseBacking = (dataList[state.noteState.criteriaIndex].detail as DetailChord).backing;
                                state.noteState.selectedList.forEach((index) => {
                                    (dataList[index].detail as DetailChord).backing = JSON.parse(JSON.stringify(baseBacking));
                                });
                                dispatcher.score.setNoteData({ ...state.noteData, dataList });
                                dispatcher.score.releaseCriteria();
                                dispatcher.score.apllyFlashList();
                            }
                                break;
                        }
                    }
                }
                    break;
                case FloatWndState.CHORD_PALETTE: {
                    switch (e.key) {
                        case 'Escape':
                        case 'c':
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                            break;
                        case 'ArrowRight':
                            dispatch(ActionType.SET_DEGREE_INDEX, { pos: focusIndex, degreeIndex: detail.degreeIndex + 1 });
                            break;
                        case 'ArrowLeft':
                            dispatch(ActionType.SET_DEGREE_INDEX, { pos: focusIndex, degreeIndex: detail.degreeIndex - 1 });
                            break;
                        case '1':
                            dispatch(ActionType.SET_DEGREE_INDEX, { pos: focusIndex, degreeIndex: detail.degreeIndex - 1 });
                            break;
                        case 'Enter': {
                            dispatcher.chord.setCurChordParam();
                        }
                            break;
                    }
                }
                    break;
                case FloatWndState.BACKING_PALETTE: {
                    const isCorrect = state.noteState.backingPaletteState.isCorrect;
                    switch (e.key) {
                        case 'Escape':
                        case 'b':
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                            break;
                        case 'ArrowLeft': {
                            if (!dispatcher.backing.isFirstCurLen()) {
                                const cur = dispatcher.backing.getCurLenIndex();
                                dispatcher.backing.updateLenIndex(cur - 1);
                            }
                        }
                            break;
                        case 'ArrowRight': {
                            if (!dispatcher.backing.isLastCurLen()) {
                                const cur = dispatcher.backing.getCurLenIndex();
                                dispatcher.backing.updateLenIndex(cur + 1);
                            }
                        }
                            break;
                        case 'ArrowUp': {
                            const curVolume = dispatcher.backing.getCurVelocity();
                            if (curVolume < 10) {
                                dispatcher.backing.updateCurVolume(curVolume + 1);
                            }
                        }
                            break;
                        case 'ArrowDown': {
                            const curVolume = dispatcher.backing.getCurVelocity();
                            if (curVolume > 1) {
                                dispatcher.backing.updateCurVolume(curVolume - 1);
                            }
                        }
                            break;
                        case 'r': {
                            dispatcher.backing.changeLayerIndex();
                        }
                            break;
                        case '1': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN16); break;
                        case '2': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN8); break;
                        case '3': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN4); break;
                        case '4': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN2); break;
                        case '5': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN1); break;
                        case '.': {
                            if (dispatcher.backing.isAcceptHalf()) {
                                const curLenBean = dispatcher.backing.getCurLenBean();
                                curLenBean.isHalf = !curLenBean.isHalf;
                                curLenBean.isDiv3 = false;
                                dispatcher.backing.setCurrentLenBean(curLenBean);
                            }
                        }
                            break;
                        case '/': {
                            if (dispatcher.backing.isAcceptDiv3()) {
                                const curLenBean = dispatcher.backing.getCurLenBean();
                                curLenBean.isDiv3 = !curLenBean.isDiv3;
                                curLenBean.isHalf = false;
                                dispatcher.backing.setCurrentLenBean(curLenBean);
                            }
                        }
                            break;
                        case 'a': {
                            dispatcher.backing.addLen();
                        }
                            break;
                        case 'Delete': {
                            if (dispatcher.backing.getSideScript().lens.length > 1) {
                                dispatcher.backing.delLen();
                            }
                        }
                            break;

                        case ' ': {
                            if (isCorrect) {
                                dispatcher.backing.play(false);
                            }
                        }
                            break;
                        // 
                        case 'Enter': {
                            if (isCorrect) {
                                dispatcher.backing.setCurBackingScript();
                            }
                        }
                            break;
                    }
                }
                    break;
            }
        }
            break;
    }

    if (state.noteState.floatwndState == FloatWndState.NONE &&
        [TypeNoteElement.Chord, TypeNoteElement.Section, TypeNoteElement.Modulation].includes(selectedData.type)) {
        if (editMode == EditMode.NORMAL) {
            switch (e.key) {
                case 'ArrowLeft': {
                    dispatcher.score.focusPreviousElement();
                }
                    break;
                case 'ArrowRight': {
                    dispatcher.score.focusNextElement();
                }
                    break;
                case 'ArrowUp': {
                    dispatcher.score.focusPreviousSection();
                }
                    break;
                case 'ArrowDown': {
                    dispatcher.score.focusNextSection();
                }
                    break;
                case 'Delete': {
                    dispatcher.score.deleteCurElement();
                }
                    break;
                case 'i': {
                    dispatcher.score.focusInitElement();
                }
                    break;
                case 'a':
                    dispatcher.score.addChordElement();
                    break;
                case 's':
                    dispatcher.score.addSectionElement();
                    break;
                case 'f':
                    dispatcher.score.addModulationElement();
                    break;
                case 'w': {
                    const detail = selectedData.detail as DetailChord;
                    dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CHORD_PALETTE });
                    dispatch(ActionType.CHORD_PALETTE_SETTER, {
                        params: {
                            ...state.noteState.chordPaletteState,
                            rootIndex: detail.degreeIndex,
                            isFlat: detail.isFlat,
                            symbolKey: detail.symbolKey,
                            sustain: detail.sustain,
                        }
                    });
                }
                    break;
                case 'z': {
                    alert(JSON.stringify(state.noteState.cacheList.length));
                    // alert(JSON.stringify(detail));
                    // dispatch(ActionType.SELECT_SCORETAB, { index: 2 });

                    // const data = state.noteData.dataList[state.noteState.focusIndex];
                    // data.detail = {"degreeIndex":2,"isFlat":false,"symbolKey":1,"sustain":4,"backing":{"useChannels":[{"octave":3,"structIndex":0},{"octave":3,"structIndex":1},{"octave":3,"structIndex":2},{"octave":4,"structIndex":0},{"octave":4,"structIndex":1},{"octave":4,"structIndex":2},{"octave":5,"structIndex":1},{"octave":5,"structIndex":2}],"layers":[{"lens":[{"len":"LEN8","isDiv3":false,"isHalf":false},{"len":"LEN8","isHalf":false,"isDiv3":false},{"len":"LEN8","isHalf":false,"isDiv3":false},{"len":"LEN8","isHalf":false,"isDiv3":false},{"len":"LEN8","isHalf":false,"isDiv3":false}],"side":"LEFT","table":[[[true],[false],[false],[false],[false]],[[false],[false],[false],[false],[false]],[[false],[false],[true],[true],[false]],[[false],[false],[false],[false],[false]],[[true],[false],[false],[false],[false]],[[false],[true],[false],[false],[false]],[[false],[false],[false],[false],[false]],[[false],[false],[false],[false],[true]]],"vels":[5,5,5,5,5]},{"lens":[{"len":"LEN1","isDiv3":false,"isHalf":false}],"side":"RIGHT","table":[[[false]],[[false]],[[false]],[[false]],[[false]],[[false]],[[false]],[[false]]],"vels":[5]}]}};
                    // dispatcher.setNoteData(state.noteData);
                }
                    break;
            }
        } else if (editMode == EditMode.PREVIEW) {

            switch (e.key) {
                case ' ': {
                    const threads = state.noteState.backingThreads;
                    threads.forEach((thr) => {
                        clearInterval(thr);
                    });
                    dispatcher.score.setNoteState({
                        ...state.noteState,
                        backingThreads: [], editMode: EditMode.NORMAL
                    });
                    state.sampler.releaseAll();
                }
                    break;
            }
        }
    }
};